.sidebar-widget-area {
  min-width: 25%;
  border-left: 1px solid black;
  padding-left: 2rem;

  i {
    width: 30px;
    font-size: 20px;
  }

  a {
    text-decoration: none;
    font-family: $font__main;
    text-transform: none;
  }

  li {
    list-style: none;
  }

  ul {
    margin: 0px;
    padding: 0px;
  }
}
